<template>
    <div class="test">
        <div class="layout-1">
            <div class="presentacion-oferta-admision">
                <div>
                    <h2 class="rtecenter"><strong>Oferta acadmica</strong></h2>
                </div>
                <div class="enlaces-admision">
                    <a href="https://utpl.edu.ec/distancia">
                        Admisiones grado Modalidad Abierta y a Distancia
                    </a>
                    <a href="https://utpl.edu.ec/presencial">
                        Admisiones grado Modalidad Presencial
                    </a>
                    <a href="https://utpl.edu.ec/maestrias">
                        Admisiones maestras
                    </a>
                </div>
            </div>

            <div class="wrapper-desplegables">
                <div class="movil-seleccione">Seleccione las opciones</div>
                <div class="section1 desplegable-opciones">
                    <div class="section">
                        <div
                            class="general"
                            id="item"
                            :class="{ selected: selectedOpt === 'item' }"
                            @click="setSelectedOpt('item')"
                        >
                            Toda la oferta
                        </div>
                    </div>
                    <h2>Oferta por rea</h2>
                    <div class="section">
                        <div
                            class="general"
                            :class="{ selected: selectedOpt === areaOpt.id }"
                            @click="setSelectedOpt(areaOpt.id)"
                            v-for="(areaOpt, index) of areaOpts"
                            :key="index"
                            :id="areaOpt.id"
                        >
                            {{ areaOpt.title }}
                        </div>
                    </div>

                    <h2>Oferta por modalidad</h2>
                    <div class="section">
                        <div
                            class="distancia"
                            id="Distancia"
                            :class="{ selected: selectedOpt === 'Distancia' }"
                            @click="setSelectedOpt('Distancia')"
                        >
                            Modalidad Abierta y a Distancia
                        </div>
                        <div
                            class="presencial"
                            id="Presencial"
                            :class="{ selected: selectedOpt === 'Presencial' }"
                            @click="setSelectedOpt('Presencial')"
                        >
                            Modalidad Presencial
                        </div>
                        <div
                            class="linea"
                            id="lnea"
                            :class="{ selected: selectedOpt === 'lnea' }"
                            @click="setSelectedOpt('lnea')"
                        >
                            En lnea
                        </div>
                    </div>
                </div>

                <div class="section2 desplegable-contenido">
                    <h2 class="desplegable-title">Grado</h2>
                    <div class="grado">
                        <div
                            :class="[
                                'item',
                                career['field_area'],
                                career['field_modalidad_carrera'][0].replace(
                                    'Modalidad',
                                    ''
                                ),
                            ]"
                            v-for="(career, index) of careers"
                            :key="index"
                        >
                            <img
                                v-if="
                                    career['field_modalidad_carrera'][0] ===
                                    ' Modalidad Abierta y a Distancia'
                                "
                                src="https://www.utpl.edu.ec/sites/default/files/distancia-icon.png"
                            />
                            <img
                                v-else
                                src="https://www.utpl.edu.ec/sites/default/files/presencial-icon.png"
                            />
                            <img
                                v-if="career['field_modalidad_carrera'][1]"
                                src="https://www.utpl.edu.ec/sites/default/files/presencial-icon.png"
                            />
                            <div v-html="career.title"></div>
                        </div>
                    </div>
                    <h2 class="desplegable-title">Maestras</h2>
                    <div class="posgrado">
                        <div class="item Empresariales Modalidad Presencial">
                            <img
                                src="https://www.utpl.edu.ec/sites/default/files/presencial-icon.png"
                            />
                            <a href="https://edes.utpl.edu.ec/MBA">
                                Administracin de Empresas mencin en
                                Innovacin-MBA</a
                            >
                        </div>
                        <div class="item Empresariales Abierta y a Distancia">
                            <img
                                src="https://www.utpl.edu.ec/sites/default/files/distancia-icon.png"
                            />
                            <a href="https://edes.utpl.edu.ec/MBA-distancia/">
                                Administracin de Empresas mencin en
                                Innovacin-MBA</a
                            >
                        </div>
                        <div v-for="(master, index) of masters" :key="index">
                            <img
                                v-if="
                                    master['field_modalidad_carrera'][0] ===
                                    ' Modalidad Abierta y a Distancia'
                                "
                                src="https://www.utpl.edu.ec/sites/default/files/distancia-icon.png"
                            />

                            <img
                                v-else
                                src="https://www.utpl.edu.ec/sites/default/files/presencial-icon.png"
                            />

                            <img
                                v-if="master['field_modalidad_carrera'][1]"
                                src="https://www.utpl.edu.ec/sites/default/files/presencial-icon.png"
                            />
                            <div v-html="master.title"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
const mastersUrl =
    "https://utpl.edu.ec/maestrias/servicios/carrerasgeneral.json";
const careersUrl =
    "https://utpl.edu.ec/carreras/servicios/carrerasgeneral.json";

export default {
    name: "academic-offer",
    data() {
        return {
            careers: [],
            masters: [],
            areaOpts: [
                {
                    id: "Empresariales",
                    title: "Facultad de Ciencias Econmicas y Empresariales",
                },
                { id: "Salud", title: "Facultad de Ciencias de la Salud" },
                {
                    id: "Arquitectura",
                    title: "Facultad de Ingenieras y Arquitectura",
                },
                {
                    id: "Naturales",
                    title: "Facultad de Ciencias Exactas y Naturales",
                },
                {
                    id: "Humanidades",
                    title: "Facultad de Ciencias Sociales, Educacin y Humanidades",
                },
            ],
            modalityOpts: [],
            selectedOpt: "item",
        };
    },
    methods: {
        getMasters() {
            axios.get(mastersUrl).then((response) => {
                this.masters = response.data;
            });
        },
        getCareers() {
            axios.get(careersUrl).then((response) => {
                this.careers = response.data;
            });
        },
        setSelectedOpt(option) {
            this.selectedOpt = option;
        },
    },
    mounted() {
        this.getCareers();
        this.getMasters();
    },
};
</script>

<style>
.test{
    background-color: blue;
}
</style>